.sidebar{
    position: sticky;
    top:80px;
    flex:0.2;
    border-radius: 10px;
    text-align:center;
    height: fit-content;
}
 .sidebar_avatar{
     margin-bottom: 10px;
 }

.sidebar_top >h4{
    color: gray;
    font-size: 12px;
}
 .sidebar_top>h2{
     font-size:18px;
 }
 .sidebar_top{
     display:flex;
     flex-direction: column;
     align-items:center;
     border-bottom: 0;
     border-top-left-radius: 10px;
     border-top-right-radius: 10px;
     background-color:white ;
     padding-bottom: 10px;
 }
 .sidebar_top > img{
     margin-bottom: -20px;
      width: 100%; 
      height: 60px;
      border-top-left-radius: 10px;
     border-top-right-radius: 10px;
     object-fit: cover;  
 }
 .sidebar_stat{
     margin-top:10px ;
     display: flex;
     justify-content:space-between;
 }
 .sidebar_stat >p {
     color: gray;
     font-size: 13px;
     font-weight: 600;
 }
 .sidebar_statNumber{
     font-weight: bold;
     color: #0a66c2 !important;
 }
 .sidebar_stats{
     padding: 10px;
     margin-bottom: 10px;
     border: 1px solid lightgray;
     background-color: white;
     border-bottom-left-radius: 10px;
     border-bottom-right-radius: 10px;
 }

 .sidebar_bottom{
     text-align: left;
     margin-bottom: 10px;
     border: 1px solid lightgray;
     background-color: white;
     border-radius: 10px;
     margin-top:10px;
 }


 .sidebar_recentItem{
     display: flex;
     font-size: 14px;
     color: gray;
     font-weight: bold;
     cursor: pointer;
 }
 .sidebar_recentItem:hover{
background-color: whitesmoke;
border-radius: 10px;
cursor: pointer;
color: black;
 }
 .sidebar_hash{
     margin-right:10px;
     margin-left: 5px;
 }
 .sidebar_bottom{
     font-size: 13px;
     padding-bottom: 10px;
 }